body {
    margin: 0px;
    background-color: yellow;
}

#root {
    height: 100%;
    width: 100%;
}

.main {
    height: 100vh;
    display: flex;
    margin: 20px;
    align-content: stretch center; 
    align-items: center;
    justify-content: center;
    flex-direction: column;
}


.meme-input {
    height: 75px;
    width: 100%;
    overflow: hidden;
    border-radius: 5px;
    border-style: solid;
    border-width: 2px;
    font-size: 50px;
}

.spongebob {
    text-align: center;
    min-height: 200px;
    width: 70%;
    padding-top: 25px;
}